}
.header{
    display: flex;
    justify-content: space-between;
    flex: 1 1;
    padding: 12px 30px;
    position: relative;
    align-items: center;
    /* background-color: cornsilk; */
}
.menu{
    display: flex;
    flex: 1 1;
    justify-content: flex-end;
    
}
.menu-icon{
    font-size: 24px;
}
.mobile-menu{
