.about{
    color: #fff;
    width: 100%;
    height: 80%;
    
}
.about-container{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 127, 80,.85);
    width: 100%;
    height: 100%;
    padding: 100px 0;
}
.about-top{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    /* background-color: beige; */
}
.about-info{
    width: 80%;
    font-size: .7rem;
    
}
.about-info .hi{
    word-spacing: 5px;
    letter-spacing: 8px;
    transition-delay:.2s; 
    transition:1s ease in;
    -webkit-animation: txtFlyIn 1s linear;
            animation: txtFlyIn 1s linear;
}
.about-info .name{
    font-size: 2.5rem;
    font-weight: 600;
    letter-spacing: 8px;
    transition-delay:.2s; 
    transition:.4s ease in;
    -webkit-animation: txtName 1s linear;
            animation: txtName 1s linear; 

}
.about-info .profession{
    letter-spacing: 3px;
}
@-webkit-keyframes txtName{
    0% { 
        margin-left: 2rem;
        letter-spacing: normal; 
    } 
    100% { 
        margin-left: 0;
        letter-spacing: 8px; 
    }
}
@keyframes txtName{
    0% { 
        margin-left: 2rem;
        letter-spacing: normal; 
    } 
    100% { 
        margin-left: 0;
        letter-spacing: 8px; 
    }
}
@-webkit-keyframes txtFlyIn{
    0% { -webkit-transform:scale(1); transform:scale(1); }
    50% { -webkit-transform:scale(1.2); transform:scale(1.2); }
    100% { -webkit-transform:scale(1); transform:scale(1); }
}
@keyframes txtFlyIn{
    0% { -webkit-transform:scale(1); transform:scale(1); }
    50% { -webkit-transform:scale(1.2); transform:scale(1.2); }
    100% { -webkit-transform:scale(1); transform:scale(1); }
}


.about-bottom{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.about-pic{
    width: 50%;
    height: 50%;   
}
.picture{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #fff;
    /* filter: grayscale(100%); */
    transition-delay:3s; 
    transition:3s ease in;
    -webkit-animation: fadeIn 3s linear;
            animation: fadeIn 3s linear;
}
@-webkit-keyframes fadeIn{
    0% { opacity:0;}
    100% { opacity:1;}
}
@keyframes fadeIn{
    0% { opacity:0;}
    100% { opacity:1;}
}
.social{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 2rem 0;
}
.hire-resume-btn{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 2rem 0;
    /* background-color: blue; */
}

.hire-resume-btn a{
    text-decoration: none;
    margin: 0 1rem;
