}
.contact-container{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    margin:  0 auto;
    /* background-color: #333; */
}

.address-part{
    width: 100%;
    height: auto;
    /* background-color: bisque; */
    background-image: url('https://64.media.tumblr.com/e2ebfdac3317e8b98de82d50e105fae9/tumblr_mi7izyalKT1rn9vmdo1_500.gifv');
    /* background-image: url('https://i1.wp.com/media.giphy.com/media/tNwsGc6wB5okw/giphy.gif'); */
    /* background-image: url('https://i.gifer.com/ZJF0.gif'); */
    /* background-image: url('https://media.giphy.com/media/3o7qEafilDT4pzfniM/giphy.gif'); */
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 1.22rem;
    position: relative;
    /* margin: 0 auto; */
    z-index: -10;
}
.address-overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255,.75);
    z-index: -2;
}
.address-part-container{
    margin: 2rem;
    width: 60%;
}
.address-part-container>*{
    margin: 1rem 0;
    text-align: left;
    color: #333;
}
.address-part-container a{
    text-decoration: none;
    color: #333;
}
.address-part-container i,.address-part-container a i{
    margin-right: 10px;
    color: coral;
}

.contact-part{
    width: 100%;
    /* background-color: rgba(255, 127, 80,.4); */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.contact-part-title{
    font-size: 2rem;
    font-weight: 600;
}
.form-container{
    width: 100%;  
    /* background-color: cornflowerblue; */
}
.form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: coral;
    font-family: 'Poppins', sans-serif;
    letter-spacing: 1px;
}
::-webkit-input-placeholder{
