.skills{
    /* background-color: aqua; */
    width: 100%;
    min-height: 100vh;
    text-align: center;
    /* padding-bottom: 1rem; */
    -webkit-animation: fall .3s ease-out 1;
            animation: fall .3s ease-out 1;
}
@-webkit-keyframes fall {
    from{
        opacity: 0%;
        -webkit-transform: translate(0%,-10%);
                transform: translate(0%,-10%);
    }
    to{
        opacity: 100%;
        -webkit-transform: translate(0%,0%);
